.mainLogin {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #1cbe99;
}
/* img */
.mainLogin img {
  padding: 100px 0 30px;
}
/* input左侧icon */
.mainLogin .ivu-input {
  padding: 4px 32px;
}
.mainLogin .ivu-input-icon {
  left: 0;
}
.mainLogin .ivu-input-wrapper {
  background: hsla(0, 0%, 100%, 0.3);
  margin: 20px 0 0;
}
/* 记住密码和忘记密码 */
.mainLogin .check {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0;
  padding-left: 2px;
  width: 200px;
  color: #fff;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.mainLogin .check .ivu-checkbox-checked .ivu-checkbox-inner {
  border-color: #ffffff6c;
  background-color: #1cbe99;
}
.mainLogin .check .ivu-checkbox-focus {
  -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.486);
          box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.486);
}
.mainLogin .check span {
  margin: 0;
}
/* 登录按钮 */
.mainLogin .login-btn {
  color: #fff;
  border-color: rgba(24, 111, 98, 0.219);
  background-color: #ff9900;
}
/* 注册新账号 */
.mainLogin .new-user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 5px;
  width: 200px;
  color: #fff;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
/* 注册Modal */
.registerModal .ivu-modal-content {
  border-radius: 0;
  background-color: #ffffffe3;
}
.registerModal .ivu-input-focus :hover {
  -webkit-box-shadow: 0 0 0 2px rgba(28, 190, 153, 0.2);
          box-shadow: 0 0 0 2px rgba(28, 190, 153, 0.2);
}
.registerModal .ivu-input:hover {
  border-color: #1cbe99;
}
.registerModal .text {
  display: inline-block;
  width: 70px;
  text-align: right;
}
.registerModal .register-btn {
  color: #fff;
  border-color: rgba(24, 111, 98, 0.219);
  background-color: #1cbe99;
}

